import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import './header.scss'

export default class About extends Component {
  constructor() {
    super()
    this.navs = [
      { path: '/', label: '首页' },
      { path: '/production', label: '产品与服务' },
      { path: '/solution', label: '解决方案' },
      { path: '/document', label: '文档' },
      { path: '/about', label: '关于我们' },
    ]
  }

  render() {
    return (
      <div className='header'>
        <header className="container">
          <i className="logo"></i>
          <ul className="nav">
            {this.navs.map(nav => 
              <li className="nav-item" key={nav.path}>
                <NavLink exact activeClassName="active" className="link" to={nav.path}>{nav.label}</NavLink>
              </li>
            )}
          </ul>
        </header>
      </div>
    )
  }
}